import router from '../router'
import store from "../store"
import {
  SET_LOADING
} from "../store/mutation_types"

// --------------------------------------后台管理页面的请求----------------------------------------

export function profileRequest(config) {
  const instance = axios.create({
    baseURL: 'http://www.panzvor.com:8080/profile',
    // baseURL: 'http://192.168.100.5:8001/profile',
    timeout: 10000
  })

  let timer = null
  // 请求拦截器
  instance.interceptors.request.use(config => {
    // 显示加载动画
    timer = setTimeout(() => {
      store.commit(SET_LOADING, true)
    }, 500)
    // 判断token是否存在，存在则设置为请求头
    if (localStorage.token) {
      config.headers.token = localStorage.token;
    }
    return config;
  }, err => {
    console.log(err)
    store.commit(SET_LOADING, false)
    router.replace('/500')
    return err;
  })


  // 响应拦截器
  instance.interceptors.response.use(res => {
    // 隐藏加载动画
    clearTimeout(timer)
    store.commit(SET_LOADING, false)
    if (res.data.statusCode === 500) {
      store.commit(SET_LOADING, false)
      router.replace('/500')
    } else if (res.data.statusCode === 404) {
      store.commit(SET_LOADING, false)
      router.replace('/404')
    } else if (res.data.statusCode === 401) {
      store.commit(SET_LOADING, false)
      router.replace('/401')
    }
    return res
  }, err => {
    store.commit(SET_LOADING, false)
    console.log(err)
    router.replace('/500')
    return err
  })

  return instance(config)
}

// -------------------------------------------前台展示界面的请求---------------------------------------

export function request(config) {
  const instance = axios.create({
    baseURL: 'http://www.panzvor.com:8080/',
    // baseURL: 'http://192.168.100.5:8001/',
    timeout: 10000
  })

  let timer = null
  // 请求拦截器
  instance.interceptors.request.use(config => {
    // 显示加载动画
    timer = setTimeout(() => {
      store.commit(SET_LOADING, true)
    }, 500)
    // 判断token是否存在，存在则设置为请求头
    if (localStorage.token) {
      config.headers.token = localStorage.token;
    }
    return config;
  }, err => {
    console.log(err)
    clearTimeout(timer)
    store.commit(SET_LOADING, false)
    router.replace('/500')
    return err;
  })


  // 响应拦截器
  instance.interceptors.response.use(res => {
    // 隐藏加载动画
    clearTimeout(timer)
    store.commit(SET_LOADING, false)
    if (res.data.statusCode == 500) {
      store.commit(SET_LOADING, false)
      router.replace('/500')
    } else if (res.data.statusCode == 404) {
      console.log("404")
      store.commit(SET_LOADING, false)
      router.replace('/404')
    } else if (res.data.statusCode == 401) {
      store.commit(SET_LOADING, false)
      router.replace('/401')
    }
    return res
  }, err => {
    clearTimeout(timer)
    store.commit(SET_LOADING, false)
    router.replace('/500')
    return err
  })

  return instance(config)
}